import React, { Component } from 'react';

class Layout extends Component {
    constructor( props ) {
        super( props )
    }

    render() {
        let C = props => this.props.children;

        return (
            <React.Fragment>
                <p>This is My Layout</p>
                <C title="lmao" />
            </React.Fragment>
        )    
    }
}

export default Layout


